.carousel-cont {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  background-color: var(--color-dark);
  font-family: var(--font-family);
  width: 100%;
}
.wins-heading {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1rem;
}

.wins-heading h1 {
  font-size: 3rem;
  font-weight: bold;
  margin: 0;
  margin-bottom: 5rem;
  color: var(--color-bg);
}

.awards {
  color: var(--color-blue);
  
  font-size: 3.25rem;
  display: inline-block; /* Ensures the icon only takes up the necessary space */
}
.carousel {
  overflow: hidden;
  max-width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.inner {
  white-space: nowrap;
  transition: transform 0.3s ease-in-out;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.05);
}

.win-img {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 12px;
  height: 300px;
  width: auto;
  object-fit: cover;
}

.win-img img {
  border-radius: 12px;
  height: 300px;
  width: auto;
  object-fit: cover;
}

.carousel-buttons {
  display: flex;
  justify-content: space-evenly;
}

.button-arrow {
  background: none;
  border: none;
  cursor: pointer;
  margin-top: 20px;
  font-size: 1.5rem;
}

.button-arrow:hover {
  background: none;
  color: var(--color-blue);
}

.indicators {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
  background: none;
}

.indicators button {
  background: none;
  border: none;
  cursor: pointer;
}

@media screen and (max-width: 2560px) and (min-width:1500px) {
  .wins-heading h1
  {
    font-size: 6rem;
    margin-right: 1rem;
  }
  .awards
  {
    font-size: 6.5rem;
  }
  .win-img {
    height: 500px;
  }
}

@media screen and (max-width:1024px) {
  .wins-heading h1
  {
    font-size: 2.75rem;
    margin-right: 1rem;
  }
  .awards
  {
    font-size: 3rem;
  }
  .win-img {
    height: 200px;
  }
  .button-arrow
  {
    font-size: 1.25rem;
  }
  .indicators button
  {
    font-size: 0.75rem;
  }
}

@media screen and (max-width:768px) {
  .wins-heading h1
  {
    font-size: 2.5rem;
    margin-right: 1rem;
  }
  .awards
  {
    font-size: 2.75rem;
  }
  .win-img {
    height: 150px;
  }
}

@media screen and (max-width:550px) {
  .wins-heading h1
  {
    font-size: 2rem;
    margin-right: 1rem;
  }
  .awards
  {
    font-size: 2.25rem;
  }
  .win-img {
    height: 150px;
    
  }
  .button-arrow
  {
    font-size: 1rem;
  }
  .indicators
  {
    width: 70%;
  }
  .indicator-buttons
  {
    padding: 0;
  }
  
}

@media screen and (max-width:426px) 
{
  .win-img {
    height: 100px;
  }
  .wins-heading h1
  {
    font-size: 1.75rem;
    margin-right: 0rem;
    margin-bottom: 1.5rem;
  }
}

